:root {
  /*baclgraund color*/
  --b-dark: #2f303a;
  --b-middle: #f5f4fa;
  --b-blue: #2196f3;
  --b-white: #ffffff;
  --b-black: #000000;
  /*text color*/
  --txt-dark: #212121;
  --txt-grey: #757575;
  --txt-light: #ffffff;
  --txt-bleu: #2196f3;
  /*icon*/
  --light: #afb1b8;
  --white: #ffffff;
  --accent-background: #2196f3;
  --dark-background: rgba(255, 255, 255, 0.1);
  --light-background: #ffffff;
  /*fonts*/
  --main-font: Roboto, sans-serif;
  --secondary-font: Raleway, sans-serif;
  --timer: cubic-bezier(0.4, 0, 0.2, 1);
}

*,
::after,
::before {
  box-sizing: border-box;
}

body {
  font-family: var(--main-font);
  font-style: normal;
  color: var(--txt-grey);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  margin: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  width: 1200px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

.section {
  padding: 94px 0;
}

.logo {
  font-family: var(--secondary-font);
  color: var(--txt-bleu);
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  height: 31px;
}
.logo-dark {
  color: var(--txt-dark);
}
.logo-white {
  color: var(--txt-light);
}

.btn {
  background-color: var(--b-blue);
  font-family: var(--secondary-font);
  font-weight: bold;
  font-size: 16px;
  line-height: 1.87;
  letter-spacing: 0.06em;
  border: none;
  border-radius: 4px;
}

.head {
  padding-top: 24px;
  padding-bottom: 25px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: #eeeeee;
}
.head .container {
  display: flex;
  align-items: center;
}
.head.logo {
  margin-right: 93px;
}
.head.nav-address {
  margin-left: auto;
}

.nav {
  display: flex;
  align-items: center;
}
.nav-listitems {
  display: flex;
  align-items: center;
}
.nav-listitems:not(:last-child) {
  margin-right: 50px;
}

.menu-links {
  display: flex;
}

.link-items {
  color: var(--txt-dark);
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  display: flex;
  transition: color 250ms var(--timer);
}
.link-items:hover {
  color: var(--txt-bleu);
  transform: opasity;
}
.link-items:focus {
  color: var(--txt-bleu);
  outline: 1px solid #757575;
  transform: opasity;
}
.link-icon {
  display: flex;
  width: 16px;
  height: 16px;
  background-color: var(--light-background);
  fill: var(--txt-grey);
  margin-right: 10px;
  object-fit: contain;
  object-position: center;
  transition: fill 250ms var(--timer);
}

.address {
  display: flex;
  color: var(--txt-grey);
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  transition: color 250ms var(--timer);
}
.address:focus {
  color: var(--txt-bleu);
  outline: 1px solid #757575;
  transform: opasity;
}
.address:hover {
  color: var(--txt-bleu);
  transform: opasity;
}
.address:hover .link-icon, .address:focus .link-icon {
  fill: var(--txt-bleu);
  color: var(--txt-bleu);
  outline: none;
  transform: opasity;
}

.accent {
  color: var(--txt-bleu);
  position: relative;
}

a.accent::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  top: 45px;
  border-radius: 4px;
  background-color: var(--b-blue);
}

.hero {
  min-height: 600px;
  align-items: center;
  flex-direction: column;
}
.hero.container {
  background-color: var(--b-dark);
  background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)), url(../images/main/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 600px;
  max-width: 1600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero-descr {
  margin-top: 207px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-title {
  color: var(--txt-light);
  font-weight: 900;
  font-size: 44px;
  line-height: 1.36;
  text-align: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  width: 696px;
  margin: 0;
}

.btn-hero {
  color: var(--txt-light);
  margin-top: 30px;
  padding: 10px 32px;
  width: 200px;
  height: 50px;
  align-items: center;
  letter-spacing: 0.05em;
  transition: color 250ms var(--timer);
}
.btn-hero:hover {
  color: var(--txt-dark);
  cursor: pointer;
  transform: opasity;
}
.btn-hero:focus {
  color: var(--txt-dark);
  cursor: pointer;
  transform: opasity;
}

.footer {
  background-color: var(--b-dark);
  min-height: 252px;
  padding-top: 60px;
  padding-bottom: 60px;
}
.footer.container {
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
}
.footer-address {
  font-weight: normal;
  font-size: 14px;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: 0.03em;
  margin-top: 20px;
}
.footer.social-list-icon {
  fill: var(--white);
}
.footer.social-list-link {
  background-color: var(--dark-background);
}

.contakt-box {
  display: flex;
  flex-direction: column;
  width: 231px;
  /*margin-right: 70px;*/
}

.link-addres {
  color: var(--txt-light);
  transition: color 250ms var(--timer);
}
.link-addres:hover {
  color: var(--txt-bleu);
  transform: opasity;
}
.link-addres:focus {
  color: var(--txt-bleu);
  outline: 1px solid #757575;
  transform: opasity;
}
.link-contact {
  color: rgba(255, 255, 255, 0.6);
  transition: color 250ms var(--timer);
}
.link-contact:hover {
  color: var(--txt-bleu);
  transform: opasity;
}
.link-contact:focus {
  color: var(--txt-bleu);
  outline: 1px solid #757575;
  transform: opasity;
}

.list-element:not(:last-child) {
  margin-bottom: 9px;
}

.following-box {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  /*margin-right: 93px;*/
  width: 206px;
}

.follow-mailing {
  color: var(--txt-light);
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.form-contact {
  display: flex;
  position: relative;
}

.mailing-input {
  width: 358px;
  height: 50px;
  background-color: var(--b-dark);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  margin-right: 12px;
  padding-left: 16px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.mailing-input:focus {
  outline: none;
  color: var(--txt-grey);
  border: 1px solid var(--accent-background);
}
.mailing-box {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  width: 570px;
}
.mailing-box .btn {
  display: flex;
  width: 200px;
  height: 50px;
  padding: 10px 28px;
  color: var(--white);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  transition: color 250ms var(--timer);
}

.btn-follow-mailing:focus,
.btn-follow-mailing:hover {
  cursor: pointer;
  color: var(--txt-dark);
  transform: opasity;
}

.icon-send-fly {
  position: absolute;
  right: 28px;
  top: 13px;
  display: flex;
  fill: var(--txt-light);
}

